import React from 'react';
import {
  Div,
  FormRow,
  Button,
  Icon,
  Autocomplete,
  ToggleButton,
} from '@dnb/eufemia';
import { add_circled, trash } from '@dnb/eufemia/icons';
import { ControlProps } from '@jsonforms/core';
import LabelComp from '../Label/LabelComp';
import styles from './TransferCountryListControl.module.scss';

interface TransferCountryListControlProps extends ControlProps {
  data: any;
  handleChange(path: string, value: any): void;
  path: string;
}

const TransferCountryList = (props: TransferCountryListControlProps) => {
  const [countries, setCountries] = React.useState([{}]);

  const addCountry = (country) => {
    const newCountries = [...countries, country];
    setCountries(newCountries);
  };

  const removeCountry = (index) => {
    const newCountries = [...countries];
    newCountries.splice(index, 1);
    setCountries(newCountries);
  };

  const { uischema, handleChange, path, visible, schema, addItem } = props;

  if (!visible) {
    return null;
  }

  const dataSorted = countryMap.sort((a, b) => a.localeCompare(b));

  return (
    <Div className={uischema?.options?.border && styles.top}>
      {countries.map((todo, index) => (
        <FormRow
          direction="horizontal"
          key={index}
          bottom="medium"
          className={styles.border}
        >
          <Autocomplete
            size="medium"
            right="medium"
            placeholder={'Velg land'}
            bottom="small"
            data={dataSorted}
          />

          <FormRow direction="vertical" key={index}>
            <LabelComp>
              Hvor mange ganger per mned (ca.) kommer du til  overfre penger
              eller betale med kort?
            </LabelComp>
            <ToggleButton.Group
              top="small"
              bottom="small"
              label_direction="vertical"
            >
              <ToggleButton
                text={'05'}
                value={'05'}
                bottom="small"
                right="small"
              />
              <ToggleButton
                text={'6-20'}
                value={'6-20'}
                bottom="small"
                right="small"
              />
              <ToggleButton
                text={'Flere enn 20'}
                value={'Flere enn 20'}
                bottom="small"
                right="small"
              />
            </ToggleButton.Group>
            <Div className={styles.border} bottom="x-small" />
            <LabelComp>Hva blir totalt belp per mned (ca.)?</LabelComp>
            <ToggleButton.Group
              top="small"
              bottom="small"
              label_direction="vertical"
            >
              <ToggleButton
                text={'030 000 kr'}
                value={'030 000 kr'}
                bottom="small"
                right="small"
              />
              <ToggleButton
                text={'30 000200 000 kr'}
                value={'30 000200 000 kr'}
                bottom="small"
                right="small"
              />
            </ToggleButton.Group>
          </FormRow>

          <Button
            title="Slett"
            on_click={() => {
              console.log(index);
              removeCountry(index);
            }}
            icon={<Icon icon={trash} alt="Slett" title="Slett" />}
          />
        </FormRow>
      ))}
      <Button
        variant="tertiary"
        top="small"
        bottom="small"
        icon_position="left"
        text="Velg et land til"
        icon={<Icon icon={add_circled} />}
        on_click={() => {
          addCountry('country');
          addItem(path, 'test');
        }}
      />
    </Div>
  );
};
export default TransferCountryList;

